import { mapState, mapMutations } from 'vuex';
import uniPagination from "@dcloudio/uni-ui/lib/uni-pagination/uni-pagination.vue"
import http from '../../common/js/request.js';
import uniBadge from "@dcloudio/uni-ui/lib/uni-badge/uni-badge.vue"

export default {
	computed: mapState(['forcedLogin', 'hasLogin', 'userName', 'serverUrl', 'user']),
	data() {
		return {
			navlist: [
				'已申请','待同意', '进行中', '待支付','已完成'
			],
			currentIndex: 0,
			pages: 1,
			dataList:[],
			page:{
				pageNum:1,
				pageSize:10,
				total:'',
				pageTotal:'0'
			},
			order_list: [
					
			
			],
			classNum:{
				'0':'',
				'1':'',
				'2':'',
				'3':'',
				'4':'',
				'5':''
			},
			searchEntity:{
				status:0
			},
			status: '',	
			phoneStatus:{}
		}
	},
	components:{uniPagination,uniBadge},
	methods: {
		navselect(index){

			this.searchEntity.status=index;
			this.currentIndex=index;
			this.getDataList();
			
		},
		paginat(v){
			
			this.page.pageNum=v;
			console.log(v)
			
		},
		goDetail(o){
			
			uni.navigateTo({
				url:'../cargo/cargo/cargo?id='+o.userpointid
			})
		},
		getDataList(){
			
			let url='/order/query/condition';
			let data={
			        pageNum:this.page.pageNum,
					pageSize:this.page.pageSize,
					status:this.searchEntity.status,
					userid:this.user.id
			    }
			 let entity={};
			 entity.url=url;
			 entity.data=data;
			 const that=this;
		     http.post(entity).then(
		               res => {
						  
						 that.order_list=res.list;
						  that.order_list.forEach(p=>{
							  p.goodsimage=JSON.parse(p.goodsimage)
						  })
						 that.page.pageNum=res.pageNum;
						 that.page.pageSize=res.pageSize;
						 that.page.pageTotal=res.total;
						 
		               },
		               error => {
		                   console.log('失败');
		               }
		           );
		  	 
			
		},
		getOderNum(){
			
			let url='/order/query/getOrderNum';
			let data={
			      
					userId:this.user.id
			    }
			 let entity={};
			 entity.url=url;
			 entity.data=data;
			 const that=this;
			 http.get(entity).then(
			           res => {  
						  Object.assign(that.classNum,res)
			           },
			           error => {
			               console.log('失败');
			           }
			       );
		}
	},
	mounted() {
		const that=this;
		uni.getSystemInfo({
		    success: function (res) {
				that.phoneStatus=res
			/* 	that.getOderNum();
				that.getDataList() */
				uni.startPullDownRefresh();
		    }
		});

	},
	onPullDownRefresh() {
			        const that=this;
			        setTimeout(function () {
						that.getOderNum();
						that.getDataList();
			            uni.stopPullDownRefresh();
			        }, 1000);
	}
	
}
